import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import Google from 'assets/images/Icons/Google.png';
import Facebook from 'assets/images/Icons/Facebook.png';
import CashbackLogo from 'assets/images/common/CashbackLogo.svg';

import PiggySignUp from './PiggySignUp';

class PiggyCashback extends Component {
  render() {
    const { onClick } = this.props;
    return (
      <PiggySignUp
        header={
          <p className="main-text">
            Earn up to 5%
            <img src={CashbackLogo} alt="CashbackLogo.svg" />
            CASHBACK!
          </p>
        }
        subHeader={
          <p className="desc-text desc-padding">Sign in/ Sign up before checkout in order to redeem!</p>
        }
        buttonInner={
          <Fragment>
            <span>Sign in</span>
            <img src={Facebook} alt="Facebook.png" />
            <img src={Google} alt="Google.png" />
          </Fragment>
        }
        onClick={onClick}
      />
    );
  }
}

PiggyCashback.defaultProps = {
  onClick: () => {},
};

PiggyCashback.propTypes = {
  onClick: PropTypes.func,
};

export default PiggyCashback;
