import React, { Component } from 'react';
import { Row } from 'reactstrap';

import PropTypes from 'prop-types';

import './HeaderDescWithTypeDate.scss';

class HeaderDescWithTypeDate extends Component {
  render() {
    const {
      logo,
      logoName,
      insurerName,
      insurerDesc,
      data1,
      data1Value,
      data2,
      data2Value,
    } = this.props;

    return (
      <Row className="header-desc-with-type-date-container" noGutters>
        <div className="insurer-left-container">
          <div className="insurer-logo-container">
            <div className="company-logo-container">
              <img className="company-logo" src={logo} alt={logoName} />
            </div>
          </div>
          <div>
            <div className="insurer-desc-container">
              <p className="insurer-name">{insurerName}</p>
              <p className="insurer-desc">{insurerDesc}</p>
            </div>
          </div>
        </div>
        <div className="insurer-right-container">
          <div className="data-container">
            <div className="data">
              <p className="data-name">{data1}</p>
              <p className="data-val">{data1Value}</p>
            </div>
            <div className="data">
              <p className="data-name">{data2}</p>
              <p className="data-val">{data2Value}</p>
            </div>
          </div>
        </div>
      </Row>
    );
  }
}

HeaderDescWithTypeDate.defaultProps = {
  insurerName: '',
};

HeaderDescWithTypeDate.propTypes = {
  logo: PropTypes.string.isRequired,
  logoName: PropTypes.string.isRequired,
  insurerName: PropTypes.string,
  insurerDesc: PropTypes.string.isRequired,
  data1: PropTypes.string.isRequired,
  data1Value: PropTypes.string.isRequired,
  data2: PropTypes.string.isRequired,
  data2Value: PropTypes.string.isRequired,
};

export default HeaderDescWithTypeDate;
