/**
 * CashbackBalance.js
 * Component that displays the user's cashback balance
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import CashbackLogo from 'assets/images/common/CashbackLogo.svg';

import './CashbackBalance.scss';

/**
 * Custom Form Dropdown class
 */
class CashbackBalance extends Component {
  render() {
    const { cashbackBalance } = this.props;

    return (
      <div className="cashback-summary">
        <img src={CashbackLogo} alt="CashbackLogo.svg" />
        <p>${cashbackBalance}</p>
      </div>
    );
  }
}

CashbackBalance.defaultProps = {
};

CashbackBalance.propTypes = {
  cashbackBalance: PropTypes.string.isRequired,
};

export default CashbackBalance;
