/**
 * CheckoutWrapper.js
 * This handles the initial portion of the checkout and
 * getting the initial promo and stripe form here.
 *
 * Creates anonymous session and transaction here if
 * not adhoc.
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Checkout from './Checkout';

import { getAnonymousSession, getPromoCode, getReceipt, getTransactionStripeSession, postCheckout } from '../../../actions/insuranceActions';
import { setLoading, clearLoading } from '../../../actions/loadingScreenActions';

class CheckoutWrapper extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { checkoutButton, sessionId, transactionData } = prevState;
    const {
      getPromo,
      userToken,
      getStripe,
      charged,
      isAdHoc,
      adHocData,
    } = nextProps;

    let result = null;
    if (checkoutButton !== nextProps.checkoutButton) {
      result = {
        checkoutButton: nextProps.checkoutButton,
      };
    }

    if (sessionId !== nextProps.sessionId) {
      result = {
        ...result,
        sessionId: nextProps.sessionId,
      };

      if (charged || (isAdHoc && adHocData.status === 'paid')) {
        nextProps.getReceipt(nextProps.sessionId);
      } else {
        getPromo(userToken, nextProps.sessionId, null, false);
      }
    }

    if (transactionData !== nextProps.transactionData) {
      result = {
        ...result,
        transactionData: nextProps.transactionData,
      };

      getStripe(nextProps.sessionId, null, false);
    }

    return result;
  }

  constructor(props) {
    super(props);

    const {
      checkoutButton,
      transactionData,
      loading,
      backToDetails,
      headerData,
      mainDetails,
      subDetails,
      isAdHoc,
      params,
      wording,
      verifyParams,
    } = this.props;

    loading(backToDetails);

    if (!isAdHoc) {
      this.props.getAnonymousSession(params, verifyParams);
    }

    this.state = {
      headerData,
      mainDetails,
      subDetails,
      checkoutButton,
      sessionId: null,
      transactionData,
      wording,
    };
  }

  componentDidUpdate(prevProps) {
    const { sessionId, transactionData } = this.state;
    const { loadingEnd } = this.props;
    if ((!prevProps.sessionId || !prevProps.transactionData) && sessionId && transactionData) {
      loadingEnd();
    }
  }

  render() {
    const {
      headerData,
      mainDetails,
      subDetails,
      checkoutButton,
      sessionId,
      transactionData,
      wording,
    } = this.state;

    if (!sessionId || !transactionData) {
      return null;
    }

    const {
      getStripe,
      getPromo,
      checkout,
      promoErrorString,
      isLoggedIn,
      charged,
      userDetails,
      backToDetails,
      isAdHoc,
      adHocData,
      trackingTags,
      category,
    } = this.props;

    const userID = isLoggedIn ? userDetails.data.id : null;
    const userToken = isLoggedIn ? userDetails.data.token : null;
    const rewardPoints = isLoggedIn ? userDetails.data.reward_points : null;
    const adHocCharged = isAdHoc && adHocData.status === 'paid' ? true : null;
    const isCharged = charged || adHocCharged;
    const getPromoToUse = isCharged ? this.props.getReceipt : getPromo;

    return (
      <Checkout
        headerData={headerData}
        mainDetails={mainDetails}
        subDetails={subDetails}
        transactionData={transactionData}
        checkoutButton={checkoutButton}
        getStripe={getStripe}
        getPromo={getPromoToUse}
        sessionId={sessionId}
        promoErrorString={promoErrorString}
        isLoggedIn={isLoggedIn}
        userID={userID}
        checkout={checkout}
        charged={isCharged}
        userToken={userToken}
        rewardPoints={rewardPoints}
        backToDetails={backToDetails}
        wording={wording}
        isAdHoc={isAdHoc}
        trackingTags={trackingTags}
        category={category}
      />
    );
  }
}

CheckoutWrapper.defaultProps = {
  charged: null,
  sessionId: null,
  transactionData: null,
  promoErrorString: null,
  userDetails: null,
  checkoutButton: null,
  backToDetails: null,
  isLoggedIn: false,
  isAdHoc: false,
  adHocData: null,
  wording: null,
  trackingTags: null,
  verifyParams: null,
};

CheckoutWrapper.propTypes = {
  getStripe: PropTypes.func.isRequired,
  getPromo: PropTypes.func.isRequired,
  getReceipt: PropTypes.func.isRequired,
  checkout: PropTypes.func.isRequired,
  backToDetails: PropTypes.func,
  charged: PropTypes.bool,
  sessionId: PropTypes.string,
  transactionData: PropTypes.object,
  promoErrorString: PropTypes.string,
  isLoggedIn: PropTypes.bool,
  userDetails: PropTypes.object,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  getAnonymousSession: PropTypes.func.isRequired,
  checkoutButton: PropTypes.string,
  headerData: PropTypes.object.isRequired,
  mainDetails: PropTypes.object.isRequired,
  subDetails: PropTypes.array.isRequired,
  isAdHoc: PropTypes.bool,
  adHocData: PropTypes.object,
  params: PropTypes.object.isRequired,
  wording: PropTypes.string,
  trackingTags: PropTypes.object,
  category: PropTypes.string.isRequired,
  verifyParams: PropTypes.array,
};

const mapStateToProps = (state, ownProps) => {
  const sessionId = ownProps.isAdHoc ? ownProps.sessionId : state.insurance.sessionId;
  return ({
    checkoutButton: state.insurance.checkoutButton,
    sessionId,
    transactionData: state.insurance.transactionData,
    promoErrorString: state.insurance.promoErrorString,
    userDetails: state.userDetails,
  });
};

const mapDispatchToProps = dispatch => ({
  getAnonymousSession: (params, verifyParams) => {
    dispatch(getAnonymousSession(params, false, verifyParams));
  },
  getStripe: (sessionId, promoCode, useRewards) => {
    dispatch(getTransactionStripeSession(sessionId, promoCode, useRewards));
  },
  getPromo: (userToken, sessionId, promocode, useReward) => {
    dispatch(getPromoCode(userToken, sessionId, promocode, useReward));
  },
  getReceipt: (sessionId) => {
    dispatch(getReceipt(sessionId));
  },
  checkout: (sessionId, stripeData, userId) => {
    dispatch(postCheckout(sessionId, stripeData, userId));
  },
  loading: (callback = null) => {
    dispatch(setLoading(callback));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(CheckoutWrapper);
