/**
 * PromoCode.js
 * PromoCode field that takes in a callback
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';
import Button from 'components/FormsV3/Button/Button';

import '../TextInput/TextInput.scss';
import './PromoCode.scss';

// Height for medium dropdown
const mediumHeight = 60;
// Height for big dropdown
const bigHeight = 72;

/**
 * Custom Form Dropdown class
 */
class PromoCode extends Component {
  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    // dropList - The passed down dropdown list items
    // placeholderString - Placeholder string for input
    // size - Size of this control
    // numItemsDisplay - Max items to display on view
    const {
      size,
    } = this.props;

    // Get the control height
    const height = size === 'big' ? bigHeight : mediumHeight;

    // isOpened - Whether dropdown is opened
    // isError - Whether there is input error
    // listItems - The actual dropdown list
    // selectedString - The selected item string
    // height - Control height
    // numItemsDisplay - How many items to display
    // maxheight - Max height of the list
    // listHeight - Height of the list. (Items count + control)
    this.state = {
      height,
      isFocus: false,
    };

    this.handleFocus = this.handleFocus.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
  }

  handleFocus() {
    this.setState({
      isFocus: true,
    });
  }

  handleBlur() {
    this.setState({
      isFocus: false,
    });
  }

  render() {
    const {
      height,
      isFocus,
    } = this.state;

    const {
      name,
      desc,
      placeholderString,
      zIndex,
      onClick,
      extraInfo,
      errorString,
    } = this.props;

    const focusStyle = isFocus ? 'focus' : '';

    return (
      <FormInputWrapper
        desc={desc}
        ref={node => (this.formParent = node)}
        extraInfo={extraInfo}
        errorString={errorString}
      >
        <div
          className={`text-input-container promo-container ${focusStyle}`}
          style={{ height, zIndex }}
          onFocus={() => this.handleFocus()}
          onBlur={() => this.handleBlur()}
        >
          <div className="text-input">
            <input
              className="text"
              component="input"
              placeholder={placeholderString}
              name={name}
              ref={node => this.textInput = node}
            />
            <div className="promo-button">
              <Button
                variant="medium-flat-green"
                height={48}
                width={130}
                onClick={() => onClick(this.textInput.value)}
              >
                <p>Apply</p>
              </Button>
            </div>
          </div>
        </div>
      </FormInputWrapper>
    );
  }
}

PromoCode.defaultProps = {
  desc: null,
  extraInfo: null,
  errorString: null,
  size: null,
  onClick: () => {},
};


PromoCode.propTypes = {
  placeholderString: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  zIndex: PropTypes.number.isRequired,
  desc: PropTypes.string,
  onClick: PropTypes.func,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  size: PropTypes.string,
};

export default PromoCode;
