/**
 * CheckoutUsingCashback.js
 * Show this view when payment amount is 0.
 * Shoule be from cashback reduction to 0.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import CashbackLogo from 'assets/images/common/CashbackLogo.svg';
import Button from 'components/FormsV3/Button/Button';
import Popup from '../Popup/Popup';

import './CheckoutPopup.scss';

class CheckoutUsingCashback extends Component {
  constructor(props) {
    super(props);

    this.openCheckoutUsingCashback = this.openCheckoutUsingCashback.bind(this);
  }

  openCheckoutUsingCashback() {
    this.popupRef.openPopup();
  }

  render() {
    const { onConfirm } = this.props;
    return (
      <Popup ref={(r) => { this.popupRef = r; }}>
        <Fragment>
          <div className="check-out-popup">
            <div className="upper-container upper-margin-down">
              <p>
                <span>Purchase using </span>
                <img src={CashbackLogo} alt="CashbackLogo.svg" />
                <span className="focus-text"> Cashback</span>
              </p>
            </div>
            <div className="mid-container">
              <p>No payment is needed</p>
            </div>
            <div className="button-container">
              <Button
                className="checkout-button"
                variant="medium-flat"
                width={166}
                height={72}
                onClick={() => this.popupRef.closePopup()}
              >
                <p>Cancel</p>
              </Button>
              <Button
                className="checkout-button"
                variant="medium-color"
                width={166}
                height={72}
                onClick={() => {
                  onConfirm();
                  this.popupRef.closePopup();
                }}
              >
                <p>Confirm</p>
              </Button>
            </div>
          </div>
        </Fragment>
      </Popup>
    );
  }
}

CheckoutUsingCashback.defaultProps = {
};

CheckoutUsingCashback.propTypes = {
  onConfirm: PropTypes.func.isRequired,
};

export default CheckoutUsingCashback;
